import React from 'react'
import { Progress } from 'react-sweet-progress';
import "react-sweet-progress/lib/style.css";

export const QuoteSteps = () => {
    return (
      <div>
        <Steps current={1}>
          <Steps.Step title="first" />
          <Steps.Step title="second" />
          <Steps.Step title="third" />
        </Steps>
      </div>
    );
}
